import axios from './axios'
const queryString = require('querystring')
async function update (path, params) {
  const list = await find(path, params)
  return list
}
function find (path, params) {
  return new Promise((resolve, reject) => {
    const param = queryString.stringify(params || {})
    axios.update(`/${path}`, param).then((res) => {
      resolve(res)
    }).catch((err) => {
      reject(err)
    })
  })
}

export default update
