<template>
  <div class="contianer">
    <h3 class="title">企业名称：{{comName}}-{{name}}</h3>
    <div class="list">
        <label class="list-item"><i>所属商品计划:</i></label>
            <el-select v-model="goodsInfo.ptGoodsPlanId" placeholder="无">
              <el-option
                v-for="item in goodsPlanList"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
      </div>
      <div class="list">
        <label class="list-item require"><i>商品名称：</i></label>
        <el-input 
          placeholder="请输入商品名称"
          v-model="goodsInfo.name">
        </el-input>
      </div>
      <div class="list">
        <label class="list-item"><i>商品简介：</i></label>
        <el-input
          type="textarea"
          :rows="2"
          size="medium"
          placeholder="请输入内容"
          v-model="goodsInfo.remark">
        </el-input>
      </div>
      <div class="list">
        <span></span>
        <label for="" class="list-item require"><i>商品生效日期：</i></label>
        <el-date-picker v-model="goodsInfo.beginTime" value-format="yyyy-MM-dd" type="date" placeholder="选择日期"/>
        <span></span>
        <label for="" class="list-item require"><i>商品失效日期：</i></label>
        <el-date-picker v-model="goodsInfo.endTime" value-format="yyyy-MM-dd" type="date" placeholder="选择日期"/>
      </div>
      <div>
        <p>商品绑定优加服务：</p>
        <el-checkbox-group v-model="checkedServes" @change="handleCheckedServesChange">
          <el-checkbox v-for="serve in servesList" :label="serve" :key="serve">{{serve}}</el-checkbox>
        </el-checkbox-group>
      </div>
      <div class="list" v-if='goodsInfo.ptCashPrePay'>
        <p>押金垫付设置</p>
        <div>
          <label class="list-item"><i>服务区域:</i></label>
          <el-select v-model="province" placeholder="选择省" @change="provinceChange">
            <el-option
              v-for="item in provinceList"
              :key="item.cityCode"
              :label="item.areaName"
              :value="item.cityCode">
            </el-option>
          </el-select>
          <el-select v-model="city" placeholder="选择市区">
            <el-option
              v-for="item in cityList"
              :key="item.cityCode"
              :label="item.areaName"
              :value="item.cityCode">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="list">
        <el-button @click="submit">确定</el-button>
      </div>
  </div>
</template>

<script>
import queryList from '../../utils/queryList'
import post from '../../utils/post'
import update from '../../utils/update'
export default {
  name: "addOrEditGoods",
  data() {
    return {
      comName: "中国平安",
      goodsInfo: {},
      name: this.$route.query.type === "edit" ? "健康想看就看" : "新增商品",
      isEdit: this.$route.query.type === 'edit',
      value: "",
      goodsName: "",
      textarea: "",
      goodsInfo: [],
      goodsPlanList: [],
      checkedServes: [],
      availTime: '',
      expiryTime: '',
      province: '',
      city: '',
      provinceList: [],
      cityList: [],
      servesList: [
        "押金垫付",
        "电话医生",
        "体检服务",
        "门诊绿通",
        "住院绿通",
        "手术绿通",
        "二次诊疗"
      ]
    };
  },
  created() {
    if (this.edit) {
        this.getGoodsInfo()
      } else {
        this.getGoodsPlan()
    }
    this.getArea(-1, 'provinceList')
  },
  methods: {
    getArea(param, areaList) {
      queryList('area', {parentId: param}).then((res) => {
        if (res.data.code = '200') {
          this[areaList] = res.data.data
        } else {
          this.$message('系统繁忙，请稍后再试')
        }
      }).catch((err) => {
        console.log(err)
        this.$message('系统繁忙，请稍后再试!')
      })
    },
    provinceChange(val, option) {
      console.log(val)
      // this.getArea( , 'cityList')
    },
    getGoodsInfo() {},
    getGoodsPlan() {
      queryList('goodsPlan', {companyId: this.$route.query.id}).then((res) => {
        if (res.data.code = '200') {
          this.goodsPlanList = res.data.data
        } else {
          this.$message('系统繁忙，请稍后再试')
        }
      }).catch((err) => {
        console.log(err)
        this.$message('系统繁忙，请稍后再试!')
      })
    },
    handleCheckedServesChange(value) {
      console.log(value)
      
    },
    submit() {
        var mes = '系统繁忙，请稍后再试'
        if (this.isEdit) {
          update('goods', this.goodsInfo).then((res) => {
          console.log(res.data)
          if (res.data.code == 200) mes = '操作成功';
          this.$message(mes)
          }).catch((err) => {
            console.log(err)
            this.$message(err.data.message)
          })
        } else {
          post('goods', this.goodsInfo).then((res) => {
          console.log(res.data)
          if (res.data.code == 200) mes = '操作成功';
          this.$message(mes)
          }).catch((err) => {
            console.log(err)
            this.$message(err.data.message)
          })
        }
      }
  },
  watch: {
    checkedServes(value, oldVal) {
      this.goodsInfo.youBodyCheck = value.indexOf('体检服务') > -1 ? 1 : 0
      this.goodsInfo.youOutpatient = value.indexOf('门诊绿通') > -1 ? 1 : 0
      this.goodsInfo.youHospital = value.indexOf('住院绿通') > -1 ? 1 : 0
      this.goodsInfo.youOperation = value.indexOf('手术绿通') > -1 ? 1 : 0
      this.goodsInfo.youSecondMed = value.indexOf('二次诊疗') > -1 ? 1 : 0
      this.goodsInfo.ptCashPrePay = value.indexOf('押金垫付') > -1 ? 1 : 0
      this.goodsInfo.ptPhoneDoctor = value.indexOf('电话医生') > -1 ? 1 : 0
      console.log(this.goodsInfo)
    }
  }
};
</script>

<style lang="less" scoped>
@import url('../../assets/less/input.less');
.contianer {
  padding-left: 10px;
  text-align: left;
}
.title {
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
  padding-bottom: 5px;
}
</style>
